.container-tittle {
  display: flex;
  height: 65vh;
  justify-content: center;
  align-items: center;
  background-color: #E7C774;
}

.container-introduction {
  height: 35vh;
}

.margin-map {
  margin: 32px 16px 0px 0px;
}

.info-img {
  height: 50vh;
  width: 80vw;
}

.hr-info {
  width: 90%;
}

.completeviewport {
  height: 100vh;
  width: 100vw;
}

.info-0 {
  flex-grow: 0;
}

.info-1 {
  flex-grow: 1;
}

.animacion {
  animation-name: intro;
  animation-duration: 5s;
  animation-iteration-count: 1;
}

.header {
  background-color: #261D1A;
  height: 35px;
  padding-left: 12px;
  padding-top: 12px;
}

.header2 {
  background-color: #E7C774;
  height: 35px;
  padding-left: 12px;
  padding-top: 12px;
}

.blacklogo {
  filter: grayscale(100%);
}

.footer {
  flex-direction: column;
}

body {
  font-family: "Noto Sans", sans-serif;
  color: #261d1a;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 400;
}

.txt-sm {
  font-size: 14px;
}

.txt-md {
  font-size: 16px;
}

.txt-lg {
  font-size: 20px;
  line-height: 1.6;
}

.txt-xl {
  font-size: 32px;
  line-height: 1.2;
}

.txt-center {
  text-align: center;
}

.txt-left {
  text-align: left;
}

.txt-right {
  text-align: right;
}

.italic {
  font-style: italic;
}

.light {
  font-weight: 300;
}

.normal {
  font-weight: 400;
}

.medium {
  font-weight: 500;
}

.txt-dark {
  color: #261d1a;
}

.txt-light {
  color: #fff;
}

.txt-primary {
  color: #E7C774;
}

.uppercase {
  text-transform: uppercase;
}

.arrow {
  width: 50px;
  margin: 20px auto 40px auto;
  display: block;
  opacity: 0.5;
}

@media all and (min-width: 768px) {
  .margin-box {
    margin: 32px 45px 0px 45px;
  }

  .center {
    margin-right: auto;
    margin-left: auto;
    display: block;
  }

  .padding-media {
    padding-right: 20px;
    padding-left: 20px;
  }

  .margin-map {
    margin: 42px;
  }
}

@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 1.68;
  }

  .txt-sm {
    font-size: 16px;
    line-height: 1.68;
  }

  .txt-md {
    font-size: 18px;
    line-height: 1.68;
  }

  .txt-lg {
    font-size: 26px;
    line-height: 1.4;
  }
}

@media all and (min-width: 900px) {
  .wrapper {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 100%;
    grid-column-gap: 20px;
  }

  .completepercent {
    height: 100%;
  }

  .hr-info {
    display: none;
  }

  .b-r-solid {
    border-right: 1px solid #E7C774;
    height: 80%;
  }

  .b-l-solid {
    border-left: 1px solid #E7C774;
    padding-left: 15px;
    height: 80%;
  }

  .footer {
    flex-direction: row;
    margin: auto;
    align-items: center;
  }
}

@media (min-width: 1024px) {
  body {
    font-size: 18px;
    line-height: 1.68;
  }

  .txt-xl {
    font-size: 54px;
    font-weight: 300;
  }
}

@keyframes intro {
  from {
    opacity: 0;
    transform: scale(10) rotate(-150deg);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}
